<template>
  <div id="app">
    <!--主框架-->
    <view-box>
      <!--头部-->
      <x-header slot="header" class="header"
        :left-options="{ showBack:true}"
        title="新闻"
        :right-options="{showMore:true}"
      >
      </x-header>
        <!--<div slot="left">返回</div>-->
        <!--<div>首页</div>-->
        <!--<div slot="right">more</div>-->

      <!--头部菜单切换-->
        <sc class="subNav"
            :lock-y="true"
            :bounce="true"
            @touchstart.native.stop=""
            @touchmove.native.stop=""
            @touchend.native.stop=""
            @mousedown.native.stop=""
            @mousemove.native.stop=""
            @mouseup.native.stop="" >

            <div class="subNavDiv">
                <tab :line-width="2">
                    <tab-item
                        v-for="category in categories"
                        :selected=" category.key == categoryKey"
                        :key="category.key">
                        {{category.name}}
                    </tab-item>
                </tab>
            </div>

        </sc>

      <!--主内容-->
        <scroller
            :on-refresh="refresh"
            :on-infinite="infinite"
            ref="myScroller"
            class="my-scroller">

            <router-view></router-view>

        </scroller>


      <!--自动上下切换-->

      <!--主菜单-->
      <tabbar slot="bottom" class="tabbarIcon">
        <tabbar-item>
          <img slot="icon" src="../static/images/nav/icon_nav_button.png"/>
          <!--<x-icon type="ios-arrow-up" size="30"></x-icon>-->
          <span slot="label">最新</span>
        </tabbar-item>
        <tabbar-item show-dot>
          <img slot="icon" src="../static/images/nav/icon_nav_article.png"/>
          <span slot="label">推荐</span>
        </tabbar-item>
        <tabbar-item>
          <img slot="icon" src="../static/images/nav/icon_nav_cell.png"/>
          <span slot="label">我的</span>
        </tabbar-item>
      </tabbar>



    </view-box>

  </div>
</template>

<script>
  import{ ViewBox, XHeader, Tabbar, TabbarItem, Scroller as Sc, Tab, TabItem } from 'vux';

//  const refreshKey = ['A', 'B01', 'B02', 'B03', 'B04', 'B05', 'B06', 'B07', 'B08', 'B09', 'B010'];
//  let refreshKeyIndex = 0;
//  let page =0;
//  let prepage = 10;
  let firstLoaded = false;
//  let moreLoaded = true;
//  function getRefreshKey() {
//      refreshKeyIndex++;
//      if (refreshKeyIndex >= refreshKey.length) {
//          refreshKeyIndex = 0;
//      }
//      return refreshKey[refreshKeyIndex];
//  }

    export default {
        name: 'app',
        components:{
            ViewBox,
            XHeader,
            Tabbar,
            TabbarItem,
            Sc,
            Tab,
            TabItem
        },
        mounted() {
            this.$store.dispatch('getTopList').then(() => {
              setTimeout(() => {
                  firstLoaded = true;
              }, 300);
            });
        },
        computed: {
            categories(){
                return this.$store.getters.categories;
            },
            categoryKey(){
                return this.$store.getters.categoryKey;
            }
        },
        methods: {
            refresh(){
                console.log('刷新')

            },
            infinite(){
                console.log('加载更多');
            }

        }
    }
</script>

<style lang="less">
  @import '~vux/src/styles/reset.less';

  html,body{
    height:100%;
    width: 100%;
    overflow-x: hidden;
    margin:0;
  }
  #app {
    height:100%;

    .header{
      position: absolute;
      width:100%;
        z-index:9;
    }
    .subNav{
      margin-top: 46px;
        z-index:9;
    }
    .subNavDiv{
      width:750px;
    }

    .tabbarIcon img{
      width: 100%;
      height:100%;
    }
      .my-scroller {
          top: 90px;
      }

  }

</style>



