import Vue from 'vue';
import Vuex from 'vuex';
import VueJsonp from 'vue-jsonp'

Vue.use(Vuex);
Vue.use(VueJsonp);

export default new Vuex.Store({

    state:{
        categoryKey: 'recommend',
        categories:[
            {
                name:'推荐',
                key:'recommend'
            },
            {
                name:'社会',
                key:'society'
            },
            {
                name:'娱乐',
                key:'recreation'
            },
            {
                name:'军事',
                key:'military'
            },
            {
                name:'体育',
                key:'sports'
            }
        ],
        bannerData:[],
        newsList:[],
        moreList:[]
    },
    getters: {
        categoryKey(state) {
            return state.categoryKey;
        },
        categories(state) {
            return state.categories;
        },
        bannerData(state) {
            return state.bannerData;
        },
        newsList(state){
            return state.newsList;
        }
    },
    mutations:{
        updateBannerData(state, data) {
            state.bannerData = data;
        },
        updateNewsList(state, data){
            state.newsList = data;
        }
    },
    actions:{
        // 获取banner、新闻列表的数据；
        getTopList(store) {
            Vue.jsonp('http://3g.163.com/touch/jsonp/sy/recommend/0-9.html').then( data => {
                //console.log(data);
                if (data.code == 200) {
                    //console.log(data);
                    let bannerData = data.focus.filter( item => {
                        return item.addata == null;
                    }).map( item => {
                        return {
                            title: item.title,
                            img: item.picInfo[0].url,
                            url: item.link
                        }
                    });

                    let newsList = data.list.filter( item => {
                        return item.addata == null;
                    }).map( item => {
                        return {
                            title: item.title,
                            src: item.picInfo[0].url,
                            url: item.link,
                            desc: item.source
                        }
                    });

                    store.commit('updateBannerData', bannerData);
                    store.commit('updateNewsList',newsList);
                }
            });
        },

        // 下拉刷新数据；
        refreshList(store){
            Vue.jsonp('')
        },

        // 上拉加载数据；
        loadMoreList(store){

        }


    }

});



