<template>
    <div class="main">
            <!--banner-->
            <swiper
                :list="bannerData"
                v-model="slideIndex"
                auto
                loop
                @touchstart.native.stop=""
                @touchmove.native.stop=""
                @touchend.native.stop=""
                @mousedown.native.stop=""
                @mousemove.native.stop=""
                @mouseup.native.stop=""></swiper>

            <!--自动轮播文字-->
            <marquee class="newFont">
                <marquee-item
                    :key="i"
                    @click.native="newFontdetail(i)"
                    v-for="banner,i in bannerData" >{{banner.title}}</marquee-item>
            </marquee>

            <!-- 新闻列表-->
            <panel :list="newsList"></panel>

            <!-- 更多新闻-->
            <!--<panel v-if="moreList.length">-->

            <!--</panel>-->

            <!--<panel-->
                <!--v-if="moreList.length"-->
                <!--:list="moreList"-->
                <!--type="1"-->
            <!--&gt;</panel>-->

    </div>
</template>

<script>

import{ Swiper, Marquee, MarqueeItem, Panel } from 'vux';

//let firstLoaded = false;

export default {
    name: 'Main',
    components:{
        Swiper,
        Marquee,
        MarqueeItem,
        Panel
    },
    computed:{
        bannerData() {
            return this.$store.getters.bannerData;
        },
        newsList(){
            return this.$store.getters.newsList;
        },
        moreList() {
            return this.$store.getters.moreList;
        }
    },
    data(){
        return{
            slideIndex: 0
        }
    },
    methods: {
        newFontdetail (i) {
            window.open(this.$store.getters.bannerData[i].url);
            //window.location = this.$store.getters.bannerData[i].url;
        }
    }


}
</script>

<style lang="less">
    #app{
        .vux-slider{
            /*margin-top:90px;*/
        }
        .newFont{
            margin:5px 10px;
            color: #999;
        }
        .weui-media-box_appmsg .weui-media-box__hd{
            width: 120px;
        }
        .weui-media-box__title {
            white-space: inherit;
            font-size: 15px;
        }
    }
</style>



